body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.fixed-top{
  position:fixed;
  top:0;
  right:0;
  left:0;
  z-index:1030;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

h1.Montserrat{
  color: white;
  font-family: 'Montserrat';
}

.Montserrat{
  font-family:'Montserrat';
  height: auto;
  width: auto;
}

.navbar-padding{
  padding: 17px;
}

.navbar-brand {
  height: 75%;
  width: auto;
}

.Swiftionary{
  height: 100%;
  width: 100%;
  max-width:600px;
  max-height:122px;
}

.Jumbotron-Container{
  width:100%;
}

.App-header {
  background-color:  rgb(83, 198, 233);
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

.MainGrid{
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
  margin-top:30px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.PopupBox{
  border-radius:25px;
  padding:20px;
  text-align: left;

}

.header{
  color:black;
}

.MainText{
  text-align:center;

}


.Header{
    font-family:'Montserrat';
    height: auto;
    width: auto;
    font-size: 32;
}

p{
    font-family:'Montserrat';
    height: auto;
    width: auto;
    font-size:16;
    font-weight: lighter;
}

.PanelHeader{
    font-family:'Montserrat';
    height: auto;
    width: auto;
    font-size:16;
    margin-top:10px;
    margin-bottom:5px;
}

hr{
    margin:10px;
}

h1{
    margin-left:0px
}
  
.UserGuideGrid{
    justify-content: center;
    justify-self: center;
    margin-top:50px;
    border:solid #ffffff;
    border-width:15px;

}


